import styled from 'styled-components';
import Tab from '../../../components/Tab/Tab';
import LeverageBar from './LeverageBar';
import { BUY, SELL } from '../../../utils/Constants';
import { inject, observer } from 'mobx-react';

const TabWrapper = styled(Tab)`
  &.leveragebar-tab {
    .tab-header {
      .tab-left.active,.tab-right.active {
        height : 10px;
      }
      .tab-left.inactive,.tab-right.inactive {
        cursor : not-allowed;
      }
    }
  }
`

function OrderBar({trading,lang}){
  const tabSet = {
    [lang['buy']] : {
      content: <LeverageBar direction={BUY} lang={lang}/>,
      style: { 
        background: 'linear-gradient(180deg, rgba(172, 225, 255, 0.08) 0.16%, rgba(66, 143, 124, 0.08) 70.66%)'
      },
      borderColor :"rgba(89, 174, 153, 0.3)",
      direction: "tab-left",
      fontColor : "#59AE99",
      actived : (+trading.position.volume) < 0
    },
    [lang['sell']] : {
      content : <LeverageBar direction={SELL}  lang={lang}/>,
      style : {
        background: 'linear-gradient(180deg, rgba(206, 94, 94, 0.08)-0.02%, rgba(206, 94, 94, 0.08) 80.65%)'
      },
      borderColor:"rgba(206, 94, 94, 0.2)",
      direction:"tab-right",
      fontColor : "#CE5E5E",
      actived : (+trading.position.volume) > 0
    }
  } 
  return(
    // <div>
      <TabWrapper tabSet={tabSet} fixed width='100%' className='leveragebar-tab' height={44} size={18} inactiveBgColor='#0A172B' borderTop={true}  borderBottom={true} divider={true}/>
    // </div>
  )
}
export default inject('trading')(observer(OrderBar))